@-webkit-keyframes blink {
  0% {
    background-color: #303952;
    background-color: var(--dormant-cell);
  }
  100% {
    background-color: #f5cd79;
    background-color: var(--active-cell);
  }
}

@keyframes blink {
  0% {
    background-color: #303952;
    background-color: var(--dormant-cell);
  }
  100% {
    background-color: #f5cd79;
    background-color: var(--active-cell);
  }
}


.container {
  display: flex;
  justify-content: space-between;
  background-color: #596275;
  align-items: center;
}
.logo {
  fill: #fff;
}

.logo-container {
  margin-left: 2rem;
  padding: 1.2rem 2.5rem;
  display: flex;
  align-items: center;
}
.title-container {
  margin-right: 2rem;
  font-size: 2rem;
  -webkit-transition: fill 0.3s;
  transition: fill 0.3s;
}
.title-container:hover {
  fill: #fff;
}
.title {
  text-transform: uppercase;
  font-size: 2rem;
  color: #fff;
  cursor: default;
}

.instructions-container {
  width: 70%;
  margin: 0 auto;
}

.instructions {
  padding: 4rem;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#778beb), to(#778beb));
  background-image: -webkit-linear-gradient(#778beb, #778beb);
  background-image: linear-gradient(#778beb, #778beb);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 0, 0 50%);
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 50%);
}

.instructions-below {
  padding: 4rem;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#778beb), to(#778beb));
  background-image: -webkit-linear-gradient(#778beb, #778beb);
  background-image: linear-gradient(#778beb, #778beb);
  -webkit-clip-path: polygon(0 87%, 100% 0, 100% 100%, 0% 100%);
  clip-path: polygon(0 87%, 100% 0, 100% 100%, 0% 100%);
}

.instructions-container h2 {
  padding: 1rem 0;
  font-size: 3rem;
  text-transform: uppercase;
}

.instructions-container p {
  font-size: 1.7rem;
  padding: 2rem;
  line-height: 2.5rem;
}

.rec {
  font-size: 4rem;
  color: red;
}

.censor {
  background-color: #000;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}

.censor:hover {
  background-color: transparent;
}

